.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
  background: url("./images/index-bg.jpg") no-repeat center center fixed;
  background-size:cover;
}
@media only screen and (max-width:600px){
  body{
    background-position-x:20%;
  }
}

.introduction{
  overflow-wrap:normal;
  font-size: 100px;
  text-align:right;
  line-height:80%;
  float:right;
}
.index{
  position: fixed;
  bottom: 0;
  right: 0;
  width: 50%;
  height:auto;
  text-shadow:2px 2px rgba(250,250,250,1);
}

.home-nav{
  padding: 0px;
  margin: 0;
  
}

@media (min-width:600px){
  .home-nav{
    visibility:visible;
  }
}

.home-nav ul {
  list-style: none;
  padding-right: 15px;
  margin-bottom: 0;
  float: right;
}

.home-nav ul li{
  display:inline;
  white-space: nowrap;
  font-weight:bolder;
}
.home-nav ul li button{
  border: none;
  background-color: rgba(0,0,0,0);
}

