@media only screen and (min-width:600px){

  .navigation{
    position: fixed;
    top: 100px;
    right: -400px;
    display:flex;
    flex-flow: column wrap;
    visibility: hidden;
    transition: right 1s;
  }

  .navigation button{
    text-align:right;
    padding: 10px 30px;
    
    text-shadow:2px 2px rgba(250,250,250,1);
  }

  .navigation.active{
    right: 3px;
  }
  .navigation button:hover{
  }
  .toggle.active:before{
  }
}

.navigation.active{
  visibility:visible;
}
.navigation button{
  background-color:rgba(255,0,0,0);
  border:0;
 
}

.toggle{
  position: fixed;
  top:20px;
  right:20px;
  width:50px;
  height:50px;
  border-radius:50%;
  border:none;
  cursor: pointer;
  z-index:1;
  background-color:#000;
  color:rgba(255,255,255,0.93);
}
.toggle:before{
  content:'+';
  position:absolute;
  bottom:0;
  left:0;
  font-size:50px;
  width:100%;
  height:100%;
  text-align:center;
  line-height: 40px !important;
  border-radius:50%;
  transition:0.5s;
  font-weight:bold;
}


.toggle.active:before{
  transform:rotate(405deg);
}

@media only screen and (max-width:600px){
  .navigation{
    position: absolute;
    width:100%;
    top:20%;
    bottom:30%;
    
    text-align:center;
    display:flex;
    flex-flow: column wrap;
    visibility: hidden;
  }
  .navigation button{
    padding: 25px 10px;
    text-align:center;
    justify-content: space-between;
    font-size: 1.5rem;
    width:100%;
    
  }

  .overlay{
    position: fixed;
    bottom:20px;
    left:20px;
    width:50px;
    height:50px;
    border-radius:50%;
    transition: transform 0.5s;
  }
  .overlay.active{
    transform: scale(100,100);
  }
}